






import { Vue, Component } from "vue-property-decorator";
import VehicleList from "./components/VehicleList.vue";
import VehicleDetails from "./components/VehicleDetails.vue";

@Component({
  components: {
    VehicleList,
    VehicleDetails
  }
})
export default class VehicleIndex extends Vue {
  whichComponent: any = "VehicleList";
  listRowId: string = "";

  switchComponent(buttonName: string, row: any) {
    switch (buttonName) {
      case "add":
        this.whichComponent = "VehicleDetails";
        this.listRowId = "";
        break;
      case "edit":
        this.whichComponent = "VehicleDetails";
        this.listRowId = row.id;
        break;
      default:
        this.whichComponent = "VehicleList";
    }
  }
}
