

































































































import MessageBox from "@/utils/message";
import { Component, Emit, Prop, Vue } from "vue-property-decorator";
import api from "@/microApps/vehicleManage/api";

@Component({})
export default class VehicleVechcle extends Vue {
  @Prop({ type: String })
  vehicleId!: any;

  Vehicle: Array<any> = [];
  noBindVehicle: Array<any> = [];
  noBindVechcleDialogVisible: boolean = false;

  selectedSensor: Array<any> = [];

  pageParam: any = {
    size: 10,
    current: 1,
    query: this.vehicleId,
    total: 0
  };

  pageParam2: any = {
    size: 8,
    current: 1,
    total: 0,
    query: {
      name: ""
    }
  };
  loading = false;

  handleSizeChange2(val: any) {
    this.pageParam2.size = val;
    this.getNoBindList();
  }

  handleCurrentChange2(val: any) {
    this.pageParam2.current = val;
    this.getNoBindList();
  }

  handleSelectionChange(val: any) {
    this.selectedSensor = val;
  }

  //TODO 获取未绑定的设备列表
  getNoBindList() {
    this.pageParam2.query.bound = false;
    this.$http.post(api.getDevicePaging, this.pageParam2).then((res: any) => {
      if (res.data.status) {
        this.noBindVehicle = res.data.data.records;
        this.pageParam2.total = res.data.data.total;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //保存绑定
  saveBind() {
    if (this.selectedSensor.length === 0) {
      this.$message.info("请选择要绑定的设备");
      return;
    }
    this.loading = true;
    let bindObjs = this.selectedSensor.map(value => {
      return { deviceId: value.id, gpsMark: false };
    });
    this.$http
      .post(api.bindDevice + this.vehicleId, bindObjs)
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.$message.success("绑定成功");
          this.noBindVechcleDialogVisible = false;
          this.selectedSensor = [];
          this.getList();
          this.getNoBindList();
        } else {
          this.$message.error(res.data.message);
          this.noBindVechcleDialogVisible = false;
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  //TODO 获取绑定列表
  getList() {
    this.$http.get(api.getBindDevice + this.vehicleId).then((res: any) => {
      if (res.data.status) {
        this.Vehicle = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  handleSize(val: any) {
    this.pageParam.size = val;
    this.getList();
  }

  handleCurrent(val: any) {
    this.pageParam.current = val;
    this.getList();
  }

  //TODO 取消绑定
  cancelBind(data: string) {
    MessageBox.confirm("确认需要取消绑定？").then(() => {
      this.$http
        .post(api.cancelBindDevice, {
          objId: this.vehicleId,
          deviceId: data
        })
        .then((res: any) => {
          if (res.data.status) {
            this.$message.success("操作成功");
            this.getList();
          } else {
            this.$message.error(res.data.message);
          }
        });
    });
  }

  changeBindGps(data: any) {
    this.$http
      .get(api.updateGps + data.personDeviceId, {
        params: {
          gpsMark: data.gpsMark
        }
      })
      .then((res: any) => {
        if (res.data.status) {
          this.getList();
          this.$message.success("操作成功");
        } else {
          data.gpsMark = !data.gpsMark;
          this.$message.error(res.data.message);
        }
      });
  }

  bindVechcle() {
    this.selectedSensor = [];
    this.getNoBindList();
    this.noBindVechcleDialogVisible = true;
  }

  mounted() {
    this.getList();
  }
}
