

































































































































































































import { Component, Emit, Prop, Vue, Watch } from "vue-property-decorator";
import VechicleDevice from "./VehicleDevice.vue";
import Utils from "@/utils/utils";
import api from "@/microApps/vehicleManage/api";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";
import { ElTree } from "element-ui/types/tree";
import { ElForm } from "element-ui/types/form";

@Component({
  components: { VechicleDevice }
})
export default class VehicleDetails extends Vue {
  loading: boolean = false;
  @Prop({ type: String, required: true })
  listRowId!: any;

  doLoading: boolean = false;
  formLoading: boolean = false;
  // 用于父组件给子组件传值
  customOptions: Array<any> = [];
  // 车辆基本属性表单(没有包括自定义属性)
  vehicleDetailsForm: any = {};
  filterOrgId: any = "";
  // 车辆基本属性表单规则(没有包括自定义属性)
  vehicleDetailsFormRules: any = {
    // code: { required: true, message: "该选项为必填项", trigger: "change" },
    licensePlate: { required: true, message: "该选项为必填项", trigger: "change" },
    orgId: { required: true, message: "该选项为必填项", trigger: "change" },
    typeDetailId: { required: true, message: "该选项为必填项", trigger: "change" },
    status: { required: true, message: "该选项为必填项", trigger: "change" }
  };
  // 车辆类型
  vehicleTypeOptions: Array<any> = [];
  typeVisible: boolean = false;
  // 当前用户能看到的所有组织
  orgOptions: Array<any> = [];
  orgVisible: boolean = false;
  // 当前用户能看到的所有人员
  staffsOptions: Array<any> = [];
  // 是否启用
  statusOptions: Array<any> = [
    {
      value: true,
      label: "是"
    },
    {
      value: false,
      label: "否"
    }
  ];
  // 输入框是否可输入，用于查看情况
  disabled: boolean = false;
  // 隐藏确定按钮，用于查看情况
  visible: boolean = true;
  // 分页信息
  pagination: any = {
    // 查询关键字
    query: {},
    // 表格信息
    tableData: [],
    // 用于查询和显示某页数据
    current: 1,
    // 分页条数
    size: 10,
    // 总条数
    total: 0
  };
  // 传感器弹窗
  sensorDialogVisible: any = false;
  // 通用适配prop
  typeDetailProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true
  };
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("car");

  @Watch("filterOrgId")
  WatchFilterOrg(newParam: any, oldParam: any) {
    this.vehicleDetailsForm.orgId = Utils.lastOfArr(this.filterOrgId);
  }

  @Watch("listRowId", { immediate: true })
  handleIdChange() {
    this.listRowId && this.getCarInfo(this.listRowId);
    this.getPersonList("");
    this.getOrganizations();
    this.getVehicleTypes();
  }

  //切换到车辆列表页面
  @Emit("switch")
  switchToVehicleList() {}

  async getCarInfo(id: string) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.carDetail.getDetail + id);
      if (status) {
        this.vehicleDetailsForm = data;
        if (this.vehicleDetailsForm.extInfoVO) {
          this.customOptions = this.vehicleDetailsForm.extInfoVO;
        }
        this.filterOrgId = this.vehicleDetailsForm.orgId == null ? [] : this.vehicleDetailsForm.orgId.split(",");
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return this.$message.error("请求失败，请重试！");
    }
  }

  // 获取车辆类型
  getVehicleTypes() {
    this.typeVisible = true;
    this.$http.get(api.findTypeTree + "/car").then((response: any) => {
      if (response.data.status) {
        let data = response.data.data;
        this.vehicleTypeOptions = data ? data : [];
        // this.$nextTick(() => {
        //   let typeDetailId = this.vehicleDetailsForm.typeDetailId;
        //   typeDetailId && (this.$refs.typeTree as ElTree).setCheckedKeys([typeDetailId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 类型树只允许单选
  handleTypeChange(data: any, checked: boolean, node: boolean) {
    if (checked) {
      (this.$refs.typeTree as any).setCheckedKeys([data.id]);
    }
  }

  // 确定类型
  saveType() {
    // let typeDetailIds = (this.$refs.typeTree as ElTree).getCheckedKeys();
    // this.$set(this.vehicleDetailsForm, "typeDetailId", typeDetailIds.length ? typeDetailIds[0] : "");
    (this.$refs.vehicleDetailsForm as ElForm).validateField("typeDetailId", () => {});
    // this.typeVisible = false;
    if (this.vehicleDetailsForm.typeDetailId) {
      this.getCustomOptions();
    }
  }

  // 获取组织类型
  getOrganizations() {
    this.orgVisible = true;
    this.$http.get(api.getNewOrgTree).then((response: any) => {
      if (response.data.status) {
        this.orgOptions = response.data.data;
        // this.$nextTick(() => {
        //   (this.$refs.orgTree as ElTree).setCheckedKeys([this.vehicleDetailsForm.orgId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 组织树只允许单选
  handleOrgChange(data: any, checked: boolean, node: boolean) {
    if (checked) {
      (this.$refs.orgTree as any).setCheckedKeys([data.id]);
    }
  }

  // 确定类型
  saveOrg() {
    // let typeDetailIds = (this.$refs.orgTree as ElTree).getCheckedKeys();
    // if (typeDetailIds.length) {
    //   this.vehicleDetailsForm.orgId = typeDetailIds[0];
    // } else {
    //   this.vehicleDetailsForm.orgId = "";
    // }
    // this.orgVisible = false;
  }

  // 获得自定义属性
  getCustomOptions() {
    this.$http.get(api.carDetail.getCustom + this.vehicleDetailsForm.typeDetailId).then((response: any) => {
      if (response.data.status) {
        this.customOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 获取所属人员列表
  getPersonList(name: string) {
    this.$http
      .post(api.getPersonPaging, {
        current: 1,
        size: 100,
        total: 0,
        query: {
          name: name,
          organizationId: this.vehicleDetailsForm.orgId
        }
      })
      .then((res: any) => {
        if (res.status) {
          if (res.data.status) {
            this.staffsOptions = res.data.data.records;
            // this.pagination.total = res.data.data.total;
          } else {
            this.$message.error(res.data.message);
          }
        }
      });
  }

  remoteMethod(query: string) {
    this.getPersonList(query);
  }

  // 打开传感器引入弹窗
  sensorIntroduction() {
    this.sensorDialogVisible = true;
  }

  // 获得组件CustomInput填充之后的数据
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["custom-input"];
    if (customInput) {
      return await customInput.formValidation();
    }
    return null;
  }

  // 保存车辆信息
  saveVehicle() {
    (this.$refs.vehicleDetailsForm as any).validate((valid: any) => {
      if (valid) {
        if (this.vehicleDetailsForm.needUpload) {
          delete this.vehicleDetailsForm.needUpload;
          (this.$refs.upload as ElUpload).submit();
        } else {
          this.doSubmit();
        }
      }
    });
  }

  async doSubmit() {
    let data = await this.getFilledCustomOptions();
    if (data) {
      this.vehicleDetailsForm.extInfo = data;
    }
    delete this.vehicleDetailsForm.extInfoVO;
    this.doLoading = true;

    let url = api.add;
    if (this.listRowId) {
      url = api.update;
    }
    this.$http.post(url, this.vehicleDetailsForm).then((response: any) => {
      this.doLoading = false;
      if (response.data.status) {
        this.$message.success(response.data.message);
        response.data.data && this.$emit("update:listRowId", response.data.data);
        this.switchToVehicleList();
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 自定义上传文件
  customHttpRequest(data: any) {
    this.loading = true;
    let fData = Utils.createUploadForm(data.file);
    this.$http.post(data.action, fData.formData, fData.config).then((response: any) => {
      this.loading = false;
      if (response.data.status) {
        this.vehicleDetailsForm.image = response.data.data.accessUrl;
        this.doSubmit();
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 上传
  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.vehicleDetailsForm, "image", file.url);
      this.vehicleDetailsForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }

  handleRemove() {
    this.vehicleDetailsForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }
}
