


















































































import { Vue, Component, Emit } from "vue-property-decorator";
import api from "@/microApps/vehicleManage/api";
import cookieUtils from "@/utils/cookie";

@Component({})
export default class VehicleList extends Vue {
  // 分页信息
  pagination: any = {
    // 查询关键字
    query: {},
    // 表格信息
    tableData: [],
    // 用于查询和显示某页数据
    current: 1,
    // 分页条数
    page: 15,
    // 总条数
    total: 0
  };
  loading: any = false;
  // 复选框选中行数据
  multipleSelection: Array<any> = [];
  // 自定义序号
  indexCustom: number = 0;
  uploadProcessFileUrl: string = api.upload;
  httpHeaders: any = {
    accessToken: "Bearer " + cookieUtils.get("accessToken")
  };
  uploadFileList: any[] = [];

  created() {
    this.getVehicleList();
  }

  // 切换到车辆详情页面（添加和编辑）
  @Emit("switch")
  switchToVehicleDetails() {}

  // 根据关键字，每页查询数量，当前页获得所有链表
  search() {
    this.pagination.current = 1;
    this.getVehicleList();
  }

  getVehicleList() {
    this.loading = true;
    this.$http
      .post(api.getPaging, this.pagination)
      .then((response: any) => {
        this.loading = false;
        if (response.data.status) {
          this.pagination.tableData = response.data.data.records;
          this.pagination.total = response.data.data.total;
          this.indexCustom = (response.data.data.current - 1) * response.data.data.size + 1;
        } else {
          this.$message.error(response.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleCurrentChange(val: number) {
    this.pagination.current = val;
    this.getVehicleList();
  }

  handleSizeChange(val: number) {
    this.pagination.size = val;
    this.getVehicleList();
  }

  // 删除车辆信息
  deleteVehicles(ids: any) {
    if (ids.length !== 0) {
      this.$confirm("是否删除车辆相关数据？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .post(api.multipleDelete, ids)
            .then((response: any) => {
              if (response.data.status) {
                this.$message.success(response.data.message);
                this.pagination.tableData.length === ids.length ? this.pagination.current-- : this.pagination.current;
                this.getVehicleList();
              } else {
                this.$message.error(response.data.message);
              }
            })
            .catch((error: any) => {
              this.$message.error("删除失败");
            });
        })
        .catch(() => {}); //$confirm框取消后，不报错
    } else {
      this.$message.warning("请选择要删除的数据！");
    }
  }

  // 单个删除
  handleDelete(row: any) {
    let ids: any = [];
    ids.push(row.id);
    this.deleteVehicles(ids);
  }

  // 批量删除
  // multipleDelete() {
  //   let ids: any = [];
  //   this.multipleSelection.forEach(item => {
  //     ids.push(item.id);
  //   });
  //   this.deleteVehicles(ids);
  // }

  // 获得所有复选框选中数据
  // getAllSelected(val: Array<any>) {
  //   this.multipleSelection = val;
  // }

  downloadExcel() {
    let a = document.createElement("a");
    a.href = api.download;
    a.click();
  }

  uploadFileSuccess(data: any, file: any) {
    const chosenFile: any = file;
    if (data.status) {
      this.$message.success(data.message);
      this.getVehicleList();
    } else {
      this.$message.error(data.message);
      this.getVehicleList();
    }
  }

  uploadFileError() {
    this.$message.error("操作失败！");
  }
}
